<template>
  <div ref="heatmap" class="heatmap-chart"></div>
</template>

<script>
import echarts from 'echarts';

export default {
  props: {
    data: {
      type: Array,
      required: true
    }
  },
  computed: {
    range() {
      const values = this.data.map(item => item[2]);
      return {
        min: Math.min(...values),
        max: Math.max(...values)
      };
    },
    xData() {
      return this.data.map(item => item[0]).filter((item, index, arr) => arr.indexOf(item) === index);
    },
    yData() {
      return this.data.map(item => item[1]).filter((item, index, arr) => arr.indexOf(item) === index);
    }
  },
  mounted() {
    const chart = echarts.init(this.$refs.heatmap);
    // console.log(this.xData)
    const options = {
      tooltip: {
        formatter(params) {
          return params.data[2].toFixed(2);
        }
      },
      grid: {
        left: 'center',
        width: 300,
        height: 300
      },
      xAxis: {
        type: 'category',
        data: this.xData,
        axisLabel: {
          color: 'rgba(255, 255, 255, 0.8)'
        }
      },
      yAxis: {
        type: 'category',
        data: this.yData,
        axisLabel: {
          color: 'rgba(255, 255, 255, 0.8)'
        }
      },
      visualMap: {
        show: true,
        min: this.range.min,
        max: this.range.max,
        itemWidth: 12,
        itemHeight: 110,
        inRange: {
          color: ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026']
        }
      },
      series: [{
        type: 'heatmap',
        data: this.data,
        progressive: 1000,
        animation: false,
        itemStyle: {
          borderWidth: 0,
          borderColor: 'transparent',
          emphasis: {
            // borderColor: '#333',
            borderWidth: 0
          }
        }
      }]
    };
    chart.setOption(options);
    const resizeHandler = () => chart.resize();
    const chartDoneHandler = () => {
      this.$emit('done');
    };
    chart.on('finished', chartDoneHandler);
    window.addEventListener('resize', resizeHandler);
    this.$on('hook:beforeDestroy', () => {
      chart.off('finished');
      chart.dispose(); // release chart resource
      window.removeEventListener('resize', resizeHandler);
    });
  }
};
</script>

<style lang="scss" scoped>
.heatmap-chart {
  width: 100%;
  height: 100%;
}
</style>
