import axios from 'axios';

function getModelData(data) {
  const result = {
    model: [],
    fem: [],
    diff: []
  };
  data.forEach(item => {
    result.model.push([item[0], item[1], item[2]]);
    result.fem.push([item[0], item[1], item[3]]);
    result.diff.push([item[0], item[1], Math.abs(item[2] - item[3])]);
  });
  return result;
}

export default class ModelRest {
  static getModelResult(params) {
    return axios.post('/api/heatmap', {
      ...params
    }).then(res => getModelData(res.data.heatmap), () => {
      return axios.get('/mock/data.json').then(res => getModelData(res.data));
    });
  }
}
