<template>
  <div class="board-settings">
  </div>
</template>

<script>
import Chess from '../services/chess';

// it emits change event to notify board settings status
export default {
  // props: {
  //   count: {
  //     type: Number,
  //     default: 10
  //   }
  // },
  data() {
    return {
      count: 10,
      board: null
    };
  },
  methods: {
    removeAllWalls() {
      this.board.hideWall('top');
      this.board.hideWall('right');
      this.board.hideWall('bottom');
      this.board.hideWall('left');
    },
    buildAllWalls() {
      this.board.showWall('top');
      this.board.showWall('right');
      this.board.showWall('bottom');
      this.board.showWall('left');
    }
  },
  mounted() {
    this.board = new Chess(this.$el);
    // left to right, top to down
    if (this.count >= 100) {
      throw new Error('Range Error: Count shoud be less than 100');
    }

    this.board.changeHandler = ({ data }) => {
      const boardSettings = data.map(item => [item.x, item.y]);
      this.$emit('change', boardSettings);
    };
    for (let index = 0; index < this.count; ++index) {
      this.board.addPiece(index % 10, Math.floor(index / 10));
    }
    this.board.disableOrbit();
    this.board.enableDrag();
    this.$emit('done');
    this.$once('hook:beforeDestroy', () => {
      this.board.destroy();
    });
  }
};
</script>

<style lang="scss" scoped>
.board-settings {
  height: 600px;
}
</style>
