<template>
  <div class="normal-settings">
    <div class="row setting">
      <div>
        <div class="hint-text"></div>
        <multi-level-dropdown class="dropdown" @change="onBoundaryConditionChange"></multi-level-dropdown>
      </div>    
      <div>
        <div class="hint-text"></div>
        <sn-select v-model="equationConstraint" :items="equationList" @change="onEquationConstraintChange"></sn-select>
      </div>   
    </div>
    <div class="row setting">
      <div>
        <div class="hint-text"></div>
        <sn-select v-model="model" :items="modelList" @change="onModelChange"></sn-select>
      </div>
      <el-button type="primary" size="small" class="model-mgt-btn" round @click="onModelManagementBtnClick">
        
      </el-button>   
    </div>
  </div>  
</template>

<script>
import MultiLevelDropdown from './MultiLevelDropdown';
import SnSelect from './SnSelect';

// it emits change event to notify all user selected settings
export default {
  data() {
    return {
      boundaryCondition: '',
      equationConstraint: '',
      model: 'ResNet',
      equationList: [''],
      modelList: ['ResNet', 'UNet', 'FPN']
    };
  },
  methods: {
    onBoundaryConditionChange(val) {
      this.boundaryCondition = val;
      this.triggerChangeEvent();
    },
    onEquationConstraintChange() {
      this.triggerChangeEvent();
    },
    onModelChange() {
      this.triggerChangeEvent();
    },
    onModelManagementBtnClick() {
      this.$router.push({ name: 'model-management' });
    },
    triggerChangeEvent() {
      this.$emit('change', {
        boundaryCondition: this.boundaryCondition,
        equationConstraint: this.equationConstraint,
        model: this.model
      });      
    }
  },
  components: {
    MultiLevelDropdown,
    SnSelect
  }
};
</script>

<style lang="scss" scoped>
.normal-settings {
  height: 144px;
  .setting {
    justify-content: space-between;
    align-items: center;
    padding: 14px 24px;
    .hint-text {
      font-size: 14px;
      color: rgba(255, 255, 255, 0.6);
    }
    .dropdown {
      margin-left: -20px;
    }
    .model-mgt-btn {
      background-color: #2e3a43;
      color: #ffffff80;
      border: none;
      &:hover {
        background-color: #2e3a43;
        color: #ffffff;
      }
    }
  }  
}
</style>
