<template>
  <main class="row main">
    <!-- need to set min height -->
    <div class="settings-panel">
      <normal-settings @change="onNormalSettingsChange"></normal-settings>
      <board-settings @change="onBoardSettingsChange" ref="boardSetting" @done="onBoardSettingsDone"></board-settings>
      <div class="row operations">
        <!-- <el-button type="primary" size="small" round @click="onRunBtnClick"></el-button> -->
      </div>
    </div>
    <div class="result-panel">
      <div class="row header">
        <span class="result-text"></span>
        <el-button type="primary" size="small" round @click="onDiffBtnClick" class="diff-btn" v-show="!show3DChart" :disabled="!heatMap">
          
        </el-button>
        <el-button type="primary" size="small" round @click="onSwithBtn" class="switch-btn" :disabled="!heatMap">
          {{swithBtnText}}
        </el-button>
      </div>
      <div class="chart-panel" v-loading="isModelLoading" :element-loading-text="modelLoadingText"
        element-loading-background="#080e14">
        <span class="title"></span>
        <bar-chart v-if="heatMap && show3DChart" :data="heatMap.model"></bar-chart>
        <heatmap-chart v-if="heatMap && !show3DChart" class="twod-chart" :data="heatMap.model"></heatmap-chart>
      </div>
      <div class="chart-panel" v-loading="isFemLoading" :element-loading-text="femLoadingText"
           element-loading-background="#080e14">
        <span class="title"></span>
        <bar-chart v-if="heatMap && show3DChart" :data="heatMap.fem"></bar-chart>
        <heatmap-chart v-if="heatMap && !show3DChart" class="twod-chart" :data="heatMap.fem"></heatmap-chart>
      </div>
    </div>
    <el-dialog :visible.sync="dialogVisible" :close-on-click-modal="false" :modal="false">
      <div class="chart-panel" v-if="dialogVisible">
        <span class="title"></span>
        <bar-chart v-if="heatMap && show3DChart" :data="heatMap.diff"></bar-chart>
        <heatmap-chart v-if="heatMap && !show3DChart" class="twod-chart" :data="heatMap.diff"></heatmap-chart>
      </div>
    </el-dialog>
  </main>
</template>

<script>
import BarChart from '../components/BarChart';
import BoardSettings from '../components/BoardSettings';
import NormalSettings from '../components/NormalSettings';
import HeatmapChart from '../components/HeatmapChart';

import modelRest from '../rest/modelRest';

export default {
  data() {
    return {
      heatMap: null,
      settings: null,
      grid: null,
      dialogVisible: false,
      isModelLoading: false,
      isFemLoading: false,
      modelLoadingText: 'Inference',
      femLoadingText: '',
      timer: null,
      show3DChart: false,
      swithBtnText: '',
      isBoardSettingDone: false
    };
  },
  methods: {
    onSwithBtn() {
      this.show3DChart = !this.show3DChart;
      if (this.show3DChart) {
        this.swithBtnText = '';
      } else {
        this.swithBtnText = '';
      }
    },
    onBoardSettingsDone() {
      this.isBoardSettingDone = true;
    },
    onRunBtnClick() {
      this.heatMap = null;
      this.show3DChart = false;
      this.dialogVisible = false;
      this.isModelLoading = true;
      this.isFemLoading = true;
      // start loading indicator
      this.femLoadingText = '';
      const indicatorPromise = this.setupLoadingText('');
      const restPromise = modelRest.getModelResult(Object.assign({}, this.settings, { grid: this.grid }));
      Promise.all([indicatorPromise, restPromise]).then(dataList => {
        this.isModelLoading = false;
        this.heatMap = dataList[1];
        this.setupLoadingText('')
          .then(() => this.setupLoadingText(''))
          .then(() => this.setupLoadingText(''))
          .then(() => this.setupLoadingText(''))
          .then(() => {
            this.isFemLoading = false;
          });
      });
    },
    setupLoadingText(text, interval = 500) {
      return new Promise(resolve => {
        setTimeout(() => {
          this.femLoadingText = text;
          resolve();
        }, interval);
      });
    },
    onBoardSettingsChange(data) {
      this.grid = data;
      // debounce
      if (this.timer) {
        clearTimeout(this.timer);
      }
      this.timer = setTimeout(() => this.onRunBtnClick(), 3000);
    },
    onNormalSettingsChange(data) {
      this.settings = data;
      if (this.isBoardSettingDone) {
        const condition = this.settings.boundaryCondition;
        if (condition.indexOf('') > -1) {
          this.$refs.boardSetting.removeAllWalls();
        } else if (condition.indexOf('') > -1) {
          this.$refs.boardSetting.buildAllWalls();
          this.$refs.boardSetting.board.hideWall('left');
        } else {
          this.$refs.boardSetting.board.blockHole('left', 3, 6);
          this.$refs.boardSetting.buildAllWalls();
          this.$refs.boardSetting.board.digHole('left', 3, 6);
        }
        this.onBoardSettingsChange(this.grid);
      }
    },
    onDiffBtnClick() {
      this.dialogVisible = true;
    }
  },
  components: {
    BarChart,
    BoardSettings,
    NormalSettings,
    HeatmapChart
  }
};
</script>
<style lang="scss" scoped>
  .main {
    .settings-panel {
      width: 600px; // could change
      background-color: #18272f;

      .operations {
        justify-content: flex-end;
        align-items: center;
        height: 88px;
        padding: 0 32px;
      }
    }

    .result-panel {
      width: calc(100% - 600px);
      padding: 0 32px;
      background-color: #0f1d24;

      .header {
        justify-content: space-between;
        margin-top: 16px;

        .result-text {
          color: white;
        }
        .switch-btn, .diff-btn {
          background-color: #2e3a43;
          color: #ffffff80;
          border: none;
          &:hover {
            background-color: #2e3a43;
            color: #ffffff;
          }
        }
      }
    }

    .chart-panel {
      position: relative;
      height: 400px;
      margin-top: 16px;
      background-color: #080e14;

      & > .title {
        position: absolute;
        top: 24px;
        left: 24px;
        color: white;
        z-index: 10;
      }
    }

    .twod-chart {
      margin: 0 auto;
    }

    /deep/ {
      .el-dialog__wrapper {
        overflow: hidden;
        right: 24px;
        left: auto;
      }

      .el-dialog {
        width: calc(100vw - 648px);
        background-color: #18272f;
      }
    }
  }
</style>
