<template>
  <el-menu mode="horizontal" menu-trigger="hover"
    @select="handleSelect"
    background-color="#000000"
    text-color="#ffffff"
    active-text-color="#ffffff"
    class="multi-level-dropdown">
    <el-submenu index="unique" class="direct-sub" popper-class="multi-popper">
      <template slot="title">
        <span class="value">{{seletedValue}}</span>
      </template>
      <el-submenu v-for="(item, index) in items" :key="index" :index="`${index}`">
        <template slot="title">{{item.name}}</template>
        <el-menu-item v-for="(subItem, subIndex) in item.values" :key="subIndex" :index="`${subIndex}`">
          {{subItem}}
        </el-menu-item>
      </el-submenu>
    </el-submenu>      
  </el-menu>
</template>

<script>
export default {
  data() {
    return {
      items: [
        {
          name: '',
          values: [
            '',
            '',
            ''
          ]
        }
      ],
      seletedValue: ';'
    };
  },
  methods: {
    handleSelect(index, path) {
      const menu = this.items[path[1]];
      this.seletedValue = `${menu.name};${menu.values[path[2]]}`;
      this.$emit('change', this.seletedValue);
    }
  },
  mounted() {
    this.$emit('change', this.seletedValue);
  }
};
</script>

<style lang="scss">
.multi-level-dropdown {
  border: none !important;
  .direct-sub > .el-submenu__title {   
    height: 24px !important; 
    background-color: #18272f !important;
    line-height: 24px !important;
  }  
  .el-submenu.is-active .el-submenu__title{
    border: none !important;
  }  
  
}
.multi-popper {
  .el-menu-item, .el-submenu__title {
    color: rgba(255, 255, 255, 0.7) !important;
    &:hover {
      background-color: rgba(255, 255, 255, 0.1) !important;
    }
  }
}

</style>
