const THREE = require('three');
const { EffectComposer } = require('three/examples/jsm/postprocessing/EffectComposer');
const { OutlinePass } = require('three/examples/jsm/postprocessing/OutlinePass');
const { RenderPass } = require('three/examples/jsm/postprocessing/RenderPass');
const { DragControls } = require('three/examples/jsm/controls/DragControls');
const { OrbitControls } = require('three/examples/jsm/controls/OrbitControls');

const BACKGROUND = '#0f151c';
const BACKGROUND_GRID_COLOR = '#1a2026';
const BOARD_COLOR = '#0f151c';
const CELL_COLOR = '#4a7386';
const CELL_HOVER_COLOR = '#1a2b34';
const PIECE_COLOR = '#29bfff';
const PIECE_BORDER_COLOR = '#89dbff';
const PIECE_FOCUS_COLOR = '#29bfff';
const TEXT_COLOR = '#52575c';
const WALL_COLOR = '#2b5162';

export default class Chess {
  constructor(container) {
    this.container = container;
    this.width = this.container.clientWidth;
    this.height = this.container.clientHeight;

    this.changeHandler = null;

    this.raf = null;

    this.chessboardWidth = 128;
    this.pieceWidth = 10;
    this.hCellCount = 10;
    this.vCellCount = 10;
    this.gap = 2;
    this.borderGap = 5;
    this.chessContentWidth = (this.pieceWidth + this.gap) * this.vCellCount - this.gap;
    this.wallWidth = 4;
    // 
    this.wallGap = 2;

    this.pieceMoving = null;
    this.pieceMovingPos = {
      x: 0,
      y: 0,
      z: 0
    };
    this.mouseDownPos = new THREE.Vector2();

    this.cells = [];
    this.pieces = [];
    this.scene = new THREE.Scene();
    this.camera = new THREE.OrthographicCamera(-this.width / 2, this.width / 2, this.height / 2, -this.height / 2, -1000, 1000);
    this.renderer = new THREE.WebGLRenderer({
      alpha: true
    });
    this.outlinePass = new OutlinePass(new THREE.Vector2(this.width, this.height), this.scene, this.camera);
    this.orbitControl = new OrbitControls(this.camera, this.renderer.domElement);
    this.walls = {
      top: {
        show: false,
        dirty: true,
        pieces: []
      },
      bottom: {
        show: false,
        dirty: true,
        pieces: []
      },
      left: {
        show: false,
        dirty: true,
        pieces: []
      },
      right: {
        show: false,
        dirty: true,
        pieces: []
      }
    };
    this.wallHoles = {
      top: [],
      bottom: [],
      left: [],
      right: []
    };

    this.dragable = false;

    this.renderer.setClearColor(new THREE.Color(BACKGROUND));

    // this.scene.add(new THREE.AxesHelper(1500));
    this.camera.zoom = 3;
    this.camera.position.x = 0;
    this.camera.position.y = 100;
    this.camera.position.z = 0;
    this.camera.updateProjectionMatrix();

    this.renderer.setSize(this.width, this.height);
    this.renderer.setPixelRatio(window.devicePixelRatio || 1);
    this.container.appendChild(this.renderer.domElement);

    const composer = new EffectComposer(this.renderer);

    const renderPass = new RenderPass(this.scene, this.camera);
    composer.addPass(renderPass);

    this.outlinePass.edgeStrength = 10;
    this.outlinePass.visibleEdgeColor.set('#fff');
    this.outlinePass.hiddenEdgeColor.set('#190a05');
    this.outlinePass.edgeGlow = 0;
    this.outlinePass.edgeThickness = 1;
    composer.addPass(this.outlinePass);

    // 
    const gridHelper = new THREE.GridHelper(1000, 50, new THREE.Color(BACKGROUND_GRID_COLOR), new THREE.Color(BACKGROUND_GRID_COLOR));
    this.scene.add(gridHelper);

    // 
    const geometry = new THREE.BoxGeometry(this.chessboardWidth, 4, this.chessboardWidth);
    const material = new THREE.MeshBasicMaterial({
      color: BOARD_COLOR
    });
    const cube = new THREE.Mesh(geometry, material);
    cube.position.x = 0;
    cube.position.y = 0;
    cube.position.z = 0;
    this.scene.add(cube);
    // 
    // const edges = new THREE.EdgesGeometry(geometry);
    // const line = new THREE.LineSegments(edges, new THREE.LineBasicMaterial({
    //   color: 'white',
    //   linewidth: 2
    // }));
    // this.scene.add(line);

    // 
    for (let i = 0; i < this.hCellCount; i++) {
      for (let j = 0; j < this.vCellCount; j++) {
        const geometry1 = new THREE.PlaneGeometry(this.pieceWidth, this.pieceWidth);
        const material1 = new THREE.MeshBasicMaterial({
          color: CELL_COLOR
        });
        const obj = new THREE.Mesh(geometry1, material1);
        obj.userData.x1 = this.borderGap + j * (this.pieceWidth + this.gap) - this.chessboardWidth / 2;
        obj.userData.x2 = obj.userData.x1 + this.pieceWidth;
        obj.userData.z1 = this.borderGap + (this.vCellCount - 1 - i) * (this.pieceWidth + this.gap) - this.chessboardWidth / 2;
        obj.userData.z2 = obj.userData.z1 + this.pieceWidth;
        obj.userData.blank = true;
        obj.userData.pos = [j, i];
        obj.rotateX(THREE.Math.degToRad(-90));
        obj.position.x = obj.userData.x1 + this.pieceWidth / 2;
        obj.position.z = obj.userData.z1 + this.pieceWidth / 2;
        obj.position.y = 2.1;
        this.scene.add(obj);
        this.cells.push(obj);
      }
    }

    // 
    const fontLoader = new THREE.FontLoader();
    fontLoader.load('/three/helvetiker_regular.typeface.json', (font) => {
      let textGeometry;
      let cell;
      const textMaterial = new THREE.MeshBasicMaterial({
        color: TEXT_COLOR
      });
      const textList = [];
      for (let i = 0; i < this.cells.length; i += this.hCellCount) {
        cell = this.cells[i];
        textList.push({
          t: `${i / this.hCellCount + 1}`,
          x: cell.userData.x1 - 18,
          y: cell.position.y,
          z: cell.position.z + 2
        });
      }
      let code = 'a'.charCodeAt(0);
      for (let i = 0; i < this.hCellCount; i++) {
        cell = this.cells[i];
        textList.push({
          t: `${String.fromCharCode(code++)}`,
          x: cell.position.x,
          y: cell.position.y,
          z: cell.userData.z2 + 14
        });
      }
      let text;
      textList.forEach((item) => {
        textGeometry = new THREE.TextGeometry(item.t, {
          font,
          size: 4,
          height: 1
        });
        text = new THREE.Mesh(textGeometry, textMaterial);
        text.position.x = item.x;
        text.position.y = item.y;
        text.position.z = item.z;
        text.rotateX(THREE.Math.degToRad(-90));
        this.scene.add(text);
      });
    });

    // eslint-disable-next-line no-underscore-dangle
    // this.mousedownListener = this._handleMousedown.bind(this);
    // this.container.addEventListener('mousedown', this.mousedownListener);

    const animate = () => {
      this.raf = requestAnimationFrame(() => {
        animate();
      });
      composer.render();
      this.orbitControl.update();
    };

    animate();
  }

  _handleMousedown(e) {
    this.mouseDownPos.x = (e.clientX / this.width) * 2 - 1;
    this.mouseDownPos.y = -(e.clientY / this.height) * 2 + 1;
    const raycaster = new THREE.Raycaster();
    raycaster.setFromCamera(this.mouseDownPos, this.camera);
    const intersects = raycaster.intersectObjects(this.scene.children);
    this.outlinePass.selectedObjects = [];
    for (let i = 0; i < this.pieces.length; i++) {
      const piece = this.pieces[i].piece;
      let found = false;
      for (let j = 0; j < intersects.length; j++) {
        if (piece === intersects[j].object) {
          piece.material.color.set(PIECE_FOCUS_COLOR);
          this.outlinePass.selectedObjects = [piece];
          this.pieceMoving = piece;
          this.pieceMovingPos.x = piece.position.x;
          this.pieceMovingPos.z = piece.position.z;
          found = true;
        }
      }
      if (!found) {
        piece.material.color.set(PIECE_COLOR);
        this.pieceMoving = null;
      } else {
        break;
      }
    }
  }

  _getPositionByIndex(xIdx, yIdx) {
    // eslint-disable-next-line no-underscore-dangle
    const cell = this._getCellByIndex(xIdx, yIdx);
    return [cell.position.x, cell.position.z];
  }

  _getCellByIndex(xIdx, yIdx) {
    return this.cells[yIdx * this.vCellCount + xIdx];
  }

  _buildWallPiece(position, start, wallLength) {
    const wall = new THREE.BoxGeometry(this.wallWidth, this.wallWidth, wallLength);
    const canvas = document.createElement('canvas');
    canvas.width = this.wallWidth;
    canvas.height = wallLength * 2;
    const ctx = canvas.getContext('2d');
    ctx.fillStyle = BACKGROUND;
    ctx.fillRect(0, 0, this.wallWidth, wallLength);
    ctx.fillStyle = WALL_COLOR;
    let y = 0;
    while (y < wallLength * 2) {
      ctx.fillRect(0, y, this.wallWidth, 1);
      y += 2;
    }
    const materials = [];
    for (let i = 0; i < 6; i++) {
      let texture;
      if (i === 4 || i === 5) {
        const tmpCanvas = canvas.cloneNode(true);
        const tmpCtx = tmpCanvas.getContext('2d');
        tmpCtx.fillStyle = BACKGROUND;
        tmpCtx.fillRect(0, 0, this.wallWidth, wallLength);
        texture = new THREE.CanvasTexture(tmpCanvas);
      } else {
        texture = new THREE.CanvasTexture(canvas);
        if (i === 0 || i === 1) {
          texture.rotation = THREE.Math.degToRad(-90);
        }
      }
      texture.magFilter = THREE.NearestFilter;
      texture.minFilter = THREE.NearestFilter;
      texture.needsUpdate = true;
      const wallPaper = new THREE.MeshBasicMaterial({
        map: texture
      });
      materials.push(wallPaper);
    }
    const wallObj = new THREE.Mesh(wall, materials);
    let x;
    let z;
    if (position === 'top') {
      wallObj.rotateY(THREE.Math.degToRad(-90));
      x = start + wallLength / 2;
      z = -this.chessContentWidth / 2 - this.wallWidth / 2 - this.wallGap;
    } else if (position === 'bottom') {
      wallObj.rotateY((THREE.Math.degToRad(-90)));
      x = start + wallLength / 2;
      z = this.chessContentWidth / 2 + this.wallWidth / 2 + this.wallGap;
    } else if (position === 'left') {
      x = -this.chessContentWidth / 2 - this.wallWidth / 2 - this.wallGap;
      z = -(start + wallLength / 2);
    } else if (position === 'right') {
      x = this.chessContentWidth / 2 + this.wallWidth / 2 + this.wallGap;
      z = -(start + wallLength / 2);
    }
    wallObj.position.x = x;
    wallObj.position.y = 0;
    wallObj.position.z = z;
    this.scene.add(wallObj);
    this.walls[position].pieces.push(wallObj);
  }

  showWall(position) {
    if (!(position in this.walls)) {
      console.log('Invalid wall position');
      return;
    }
    const wall = this.walls[position];
    wall.show = true;
    if (wall.dirty) {
      wall.pieces.forEach((wp) => {
        this.scene.remove(wp);
      });
      wall.pieces.length = 0;
      const wallHoles = this.wallHoles[position];
      if (wallHoles.length === 0) {
        // eslint-disable-next-line no-underscore-dangle
        this._buildWallPiece(position, -this.chessContentWidth / 2, this.chessContentWidth);
      } else {
        let start;
        let wallLength;
        let nextHole;
        wallHoles.forEach((hole, i) => {
          if (i === 0 && hole[0] > 0) {
            start = -this.chessContentWidth / 2;
            wallLength = (this.pieceWidth + this.gap) * hole[0];
            // eslint-disable-next-line no-underscore-dangle
            this._buildWallPiece(position, start, wallLength);
          }
          if (i !== wallHoles.length - 1) {
            nextHole = wallHoles[i + 1];
            if (hole[1] + 1 < nextHole[0]) {
              start = -this.chessContentWidth / 2 + (this.pieceWidth + this.gap) * (hole[1] + 1) - this.gap;
              wallLength = (nextHole[0] - hole[1] - 1) * (this.pieceWidth + this.gap) + this.gap;
              // eslint-disable-next-line no-underscore-dangle
              this._buildWallPiece(position, start, wallLength);
            }
          } else if (hole[1] < this.hCellCount - 1) {
            start = -this.chessContentWidth / 2 + (hole[1] + 1) * (this.pieceWidth + this.gap) - this.gap;
            wallLength = (this.vCellCount - 1 - hole[1]) * (this.pieceWidth + this.gap);
            // eslint-disable-next-line no-underscore-dangle
            this._buildWallPiece(position, start, wallLength);
          }
        });
      }
      wall.dirty = false;
    } else {
      this.walls[position].pieces.forEach((wallPiece) => {
        this.scene.add(wallPiece);
      });
    }
  }

  hideWall(position) {
    if (!(position in this.walls)) {
      console.log('Invalid wall position');
      return;
    }
    const wall = this.walls[position];
    wall.show = false;
    wall.pieces.forEach((wp) => {
      this.scene.remove(wp);
    });
  }

  addPiece(xIdx, yIdx, data = null) {
    // eslint-disable-next-line no-underscore-dangle
    const tmpCell = this._getCellByIndex(xIdx, yIdx);
    if (!tmpCell) {
      console.warn(`Invalid xIdx: ${xIdx}, yIdx: ${yIdx}`);
      return;
    }
    if (!tmpCell.userData.blank) {
      console.warn('The position is not blank');
      return;
    }
    tmpCell.userData.blank = false;
    const box = new THREE.BoxGeometry(this.pieceWidth, this.pieceWidth, this.pieceWidth);
    const material = new THREE.MeshBasicMaterial({
      color: PIECE_COLOR
    });
    const cube = new THREE.Mesh(box, material);
    cube.userData.data = data;
    cube.userData.pos = [xIdx, yIdx];
    const edges = new THREE.EdgesGeometry(box);
    const line = new THREE.LineSegments(edges, new THREE.LineBasicMaterial({
      color: PIECE_BORDER_COLOR,
      linewidth: 1
    }));
    // eslint-disable-next-line no-underscore-dangle
    const [x, z] = this._getPositionByIndex(xIdx, yIdx);
    cube.position.x = x;
    line.position.x = x;
    cube.position.y = 2.1 + this.pieceWidth / 2 + 1;
    line.position.y = cube.position.y;
    cube.position.z = z;
    line.position.z = z;
    this.scene.add(cube);
    this.scene.add(line);

    const control = new DragControls([cube], this.camera, this.renderer.domElement);
    control.addEventListener('dragstart', (e) => {
      const obj = e.object;
      this.pieceMoving = obj;
      this.pieceMovingPos = {
        x: obj.position.x,
        y: obj.position.y,
        z: obj.position.z
      };
    });
    control.addEventListener('drag', (e) => {
      const pos = e.object.position;
      e.object.position.y = this.pieceMovingPos.y;
      line.position.x = pos.x;
      line.position.y = this.pieceMovingPos.y;
      line.position.z = pos.z;
      // eslint-disable-next-line no-shadow
      this.cells.forEach((cell) => {
        const ud = cell.userData;
        if (pos.x >= ud.x1 && pos.x <= ud.x2 && pos.z >= ud.z1 && pos.z <= ud.z2) {
          cell.material.color.set(CELL_HOVER_COLOR);
        } else {
          cell.material.color.set(CELL_COLOR);
        }
      });
    });
    control.addEventListener('dragend', (e) => {
      const pieceMoving = e.object;
      const pos = e.object.position;
      let found = false;
      for (let i = 0; i < this.cells.length; i++) {
        const cell = this.cells[i];
        cell.material.color.set(CELL_COLOR);
        const ud = this.cells[i].userData;
        if (pos.x >= ud.x1 && pos.x <= ud.x2 && pos.z >= ud.z1 && pos.z <= ud.z2 && ud.blank) {
          pieceMoving.position.x = cell.position.x;
          pieceMoving.position.y = this.pieceMovingPos.y;
          pieceMoving.position.z = cell.position.z;
          ud.blank = false;
          // cell
          const [aIdx, bIdx] = pieceMoving.userData.pos;
          // eslint-disable-next-line no-underscore-dangle
          const originCell = this._getCellByIndex(aIdx, bIdx);
          originCell.userData.blank = true;
          // 
          pieceMoving.userData.pos = [ud.pos[0], ud.pos[1]];
          found = true;
          if (this.changeHandler) {
            this.changeHandler({
              data: this.getData()
            });
          }
        }
      }
      if (!found) {
        pieceMoving.position.x = this.pieceMovingPos.x;
        pieceMoving.position.y = this.pieceMovingPos.y;
        pieceMoving.position.z = this.pieceMovingPos.z;
      }
      line.position.x = pieceMoving.position.x;
      line.position.y = pieceMoving.position.y;
      line.position.z = pieceMoving.position.z;
      this.pieceMoving = null;
    });

    if (!this.dragable) {
      control.deactivate();
    }

    this.pieces.push({
      piece: cube,
      moveControl: control
    });
    if (this.changeHandler) {
      this.changeHandler({
        data: this.getData()
      });
    }
  }

  enableDrag() {
    this.pieces.forEach((item) => {
      item.moveControl.activate();
    });
  }

  disableDrag() {
    this.pieces.forEach((item) => {
      item.moveControl.deactivate();
    });
  }

  enableOrbit() {
    this.orbitControl.enabled = true;
  }

  disableOrbit() {
    this.orbitControl.enabled = false;
  }

  zoom(inc) {
    this.camera.zoom += inc;
    this.camera.updateProjectionMatrix();
  }

  resize(w, h) {
    this.width = w;
    this.height = h;
    this.camera.left = -this.width / 2;
    this.camera.right = this.width / 2;
    this.camera.top = this.height / 2;
    this.camera.bottom = -this.height / 2;
    this.camera.updateProjectionMatrix();
    this.renderer.setSize(w, h);
  }

  digHole(position, startIdx, endIdx) {
    if (!(position in this.wallHoles)) {
      console.log('Invalid wall position');
      return;
    }
    if (startIdx > endIdx || startIdx < 0 || endIdx > this.hCellCount - 1) {
      console.log('Invalid hole index');
      return;
    }
    const wallHoles = this.wallHoles[position];
    if (wallHoles.length === 0) {
      wallHoles.push([startIdx, endIdx]);
    } else {
      let invalid = true;
      let wallHole;
      for (let i = 0; i < wallHoles.length; i++) {
        wallHole = wallHoles[i];
        if (i === 0 && endIdx <= wallHole[0]) {
          wallHoles.unshift([startIdx, endIdx]);
          invalid = false;
          break;
        }
        if (startIdx >= wallHole[1] && (i === wallHoles.length - 1 || endIdx <= wallHoles[i + 1][0])) {
          wallHoles.splice(i + 1, 0, [startIdx, endIdx]);
          invalid = false;
          break;
        }
      }
      if (invalid) {
        console.log('Invalid hole index');
        return;
      }
    }
    const wall = this.walls[position];
    wall.dirty = true;
    if (wall.show) {
      this.showWall(position);
    }
  }

  blockHole(position, start) {
    if (!(position in this.wallHoles)) {
      console.log('Invalid wall position');
      return;
    }
    const wallHoles = this.wallHoles[position];
    for (let i = 0; i < wallHoles.length; i++) {
      if (wallHoles[i][0] === start) {
        wallHoles.splice(i, 1);
        const wall = this.walls[position];
        wall.dirty = true;
        if (wall.show) {
          this.showWall(position);
        }
        break;
      }
    }
  }

  blockAllHoles(position) {
    if (!(position in this.wallHoles)) {
      console.log('Invalid wall position');
      return;
    }
    const wallHoles = this.wallHoles[position];
    if (wallHoles.length > 0) {
      wallHoles.length = 0;
      const wall = this.walls[position];
      wall.dirty = true;
      if (wall.show) {
        this.showWall(position);
      }
    }
  }

  getData() {
    return this.pieces.map((item) => {
      const ud = item.piece.userData;
      return {
        x: ud.pos[0],
        y: ud.pos[1],
        data: ud.data
      };
    });
  }

  destroy() {
    if (this.raf !== null) {
      window.cancelAnimationFrame(this.raf);
      this.raf = null;
    }
    let tmpWall;
    ['top', 'bottom', 'left', 'right'].forEach((pos) => {
      tmpWall = this.walls[pos];
      if (tmpWall.show) {
        tmpWall.pieces.forEach((wp) => {
          this.scene.remove(wp);
        });
      }
      this.walls[pos] = null;
      this.wallHoles[pos].forEach((hole) => {
        this.scene.remove(hole);
      });
    });
    this.pieces.forEach((item) => {
      item.moveControl.dispose();
    });
    this.orbitControl.dispose();
    this.scene.dispose();
    // this.container.removeEventListener('mousedown', this.mousedownListener);
  }
}
