<template>
  <div ref="chart" class="bar-chart"></div>
</template>

<script>
import echarts from 'echarts';
import 'echarts-gl';

export default {
  props: {
    data: {
      type: Array,
      required: true
    }
  },
  computed: {
    range() {
      const values = this.data.map(item => item[2]);
      return {
        min: Math.min(...values),
        max: Math.max(...values)
      };
    }
  },
  mounted() {
    const chart = echarts.init(this.$refs.chart);
    const options = {
      xAxis3D: {
        type: 'value'
      },
      yAxis3D: {
        type: 'value'
      },
      zAxis3D: {
        type: 'value',
        name: '',
        min: 'dataMin',
        showMinLabel: false
        // axisLabel: {
        //   formatter: (s) => {
        //     return s.toFixed(1);
        //   }
        // }
      },
      // tooltip: {
      //   formatter: (param) => {
      //     return param.data[2].toFixed(1);
      //   }
      // },
      grid3D: {
        axisLine: {
          lineStyle: { color: '#fff' }
        },
        axisPointer: {
          lineStyle: { color: '#fff' }
        },
        viewControl: {
          // autoRotate: true
        },
        light: {
          main: {
            shadow: true,
            quality: 'ultra',
            intensity: 1.5
          }
        }
      },
      visualMap: {
        show: true,
        min: this.range.min,
        max: this.range.max,
        itemWidth: 12,
        itemHeight: 110,
        inRange: {
          color: ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026']
        }
      },
      series: [{
        type: 'bar3D',
        data: this.data,
        shading: 'lambert',
        label: {
          formatter: param => {
            return param.value[2].toFixed(1);
          }
        }
      }]
    };
    chart.setOption(options);
    const resizeHandler = () => chart.resize();
    const chartDoneHandler = () => this.$emit('done');
    chart.on('finished', chartDoneHandler);
    window.addEventListener('resize', resizeHandler);
    this.$on('hook:beforeDestroy', () => {
      chart.off('finished');
      chart.dispose(); // release chart resource
      window.removeEventListener('resize', resizeHandler);
    });
  }
};
</script>

<style lang="scss" scoped>
.bar-chart {
  width: 100%;
  height: 100%;
}
</style>
