<template>
  <el-select v-model="val" @change="onChange" class="sn-select" size="mini" popper-class="sn-select-popper">
    <el-option v-for="(item, index) in items" :key="index"
      :label="item" :value="item">
    </el-option>
  </el-select>
</template>

<script>
export default {
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: String
    },
    items: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      val: ''
    };
  },
  methods: {
    onChange(val) {
      this.$emit('change', val);
    }
  },
  mounted() {
    this.val = this.value;
  }
};
</script>

<style lang="scss" scoped>
.sn-select {
  /deep/ {
    .el-input__inner {
      height: 24px !important;
      margin-left: -15px;
      color: white;
      background-color: #18272f !important;
      border: none !important;
      line-height: 24px !important;
    }
  }
}
</style>

<style lang="scss">
.sn-select-popper {
  background-color: black !important;
  border: none !important;
  .el-select-dropdown__item {
    color: rgba(255, 255, 255, 0.7) !important;
    &.hover {
      color: white !important;
      background-color: rgba(255, 255, 255, 0.1) !important;
    }    
  }
  .popper__arrow {
    border-bottom-color: black !important;
    &::after {
      border-bottom-color: black !important;
    }
  }
}
</style>
